.hero {
  height: 100vh;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  line-height: 1.1;
}
.hero-left p {
  color: #171717;
  font-size: 45px;
  font-weight: 600;
}
.hero-left p span {
  font-size: 20px;
  font-weight: 500;
}
.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}

.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 310px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background: #ff4141;
  color: white;
  font-size: 22px;
  font-weight: 500;
}
.hero-latest-btn img {
  transform: translate(90deg);
}
.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-right img {
  width: 100%;
}
@media (max-width: 1280px) {
  .hero-left {
    padding-left: 100px;
  }
  .hero-latest-btn {
    gap: 10px;
    width: 250px;
    height: 60px;
    margin-top: 20px;
    font-size: 18px;
  }
}
@media (max-width: 1024px) {
  .hero-left {
    padding-left: 80px;
  }

  .hero-left p {
    font-size: 40px;
  }
  .hero-latest-btn {
    width: 220px;
    height: 50px;
    font-size: 16px;
  }
}
@media (max-width: 800px) {
  .hero {
    margin-top: 40px;
  }
  .hero-left {
    padding-left: 30px;
  }
  .hero-left p {
    font-size: 30px;
  }
  .hero-left p span {
    font-size: 15px;
  }
  .hero-latest-btn {
    width: 175px;
    height: 40px;
    font-size: 13px;
  }
}

@media (max-width: 500px) {
  .hero {
    flex-direction: column-reverse;
  }
  .hero-right img {
    /* display: none; */
    width: 90%;
  }
  .hero-left p {
    font-size: 35px;
  }
  .hero-left p span {
    font-size: 20px;
  }
  .hero-latest-btn {
    width: 200px;
    height: 45px;
    font-size: 14px;
  }
}
