.offers {
  width: auto;
  display: flex;
  margin: auto;
  padding: 30px 140px;
  margin-bottom: 150px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea 60%);
}
.offers-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offers-left h1 {
  color: #171717;
  font-size: 60px;
  font-weight: 600;
}
.offers-left p {
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button {
  width: fit-content;
  padding: 8px 25px;
  border-radius: 35px;
  background: #ff4141;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}
.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}
.offers-right img {
  /* width: 355px; */
  width: 100%;
  /* padding-top: 50px; */
}
@media (max-width: 1280px) {
  .offers {
    padding: 50px 120px;
    margin-bottom: 120px;
  }
  .offers-left h1 {
    font-size: 40px;
  }
  .offers-left p {
    font-size: 18px;
  }
  .offers-left button {
    font-size: 18px;
  }
  .offers-right {
    padding-top: 30px;
  }
  .offers-right img {
    width: 300px;
    /* width: 100%; */
  }
}
@media (max-width: 1024px) {
  .offers {
    padding: 40px 80px;
    margin-bottom: 80px;
  }
  .offers-left h1 {
    font-size: 30px;
  }
  .offers-left p {
    font-size: 16px;
  }
  .offers-right img {
    width: 200px;
    /* width: 100%; */
  }
  .offers-left button {
    font-size: 16px;
  }
  .offers-right {
    padding-top: 10px;
  }
}
@media (max-width: 800px) {
  .offers {
    margin-bottom: 60px;
  }
  .offers-left h1 {
    font-size: 22px;
  }
  .offers-left p {
    font-size: 13px;
  }
  .offers-right img {
    width: 180px;
    /* width: 100%; */
  }
  .offers-left button {
    margin-top: 15px;
    padding: 8px 20px;
    font-size: 13px;
  }
  .offers-right {
    padding-top: 10px;
  }
}
@media (max-width: 500px) {
  .offers {
    /* width: 95%; */
    padding: 30px 40px;
    margin-bottom: 50px;
  }
  .offers-left h1 {
    font-size: 16px;
  }
  .offers-left p {
    font-size: 10px;
  }
  .offers-right img {
    width: 120px;
    /* width: 100%; */
  }
  .offers-left button {
    padding: 6px 15px;
    font-size: 12px;
    margin-top: 12px;
  }
  .offers-right {
    padding-top: 10px;
  }
}
