.breadcrum {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #5e5e5e;
  font-size: 16px;
  font-weight: 600;
  margin: 60px 170px;
  text-transform: capitalize;
}
@media (max-width: 1280px) {
  .breadcrum {
    font-size: 14px;
    margin: 30px 50px;
  }
}
@media (max-width: 1024px) {
  .breadcrum {
    font-size: 13px;
    margin: 30px 30px;
  }
}
@media (max-width: 800px) {
  .breadcrum {
    display: none;
    font-size: 12px;
    margin: 10px;
  }
}
@media (max-width: 500px) {
  .breadcrum {
    font-size: 10px;
  }
}
